import React from 'react';
import { Route, Routes } from "react-router-dom";
import Home from "./Home/Home";
import SurveyFormHebrew from "./SurveyForm/SurveyFormHebrew";
import SurveyFormEng from "./SurveyForm/SurveyFormEng";
import SurveyFormRus from "./SurveyForm/SurveyFormRus";
import NotFound404 from "./NotFound404";

const Main = (props) => {
    const { dir, changeLanguage, currentLang } = props;

    return (
        <Routes>
            <Route
                path="/"
                element={<Home dir={dir} changeLanguage={changeLanguage} currentLang={currentLang} />}
            />
            <Route
                path="/home"
                element={<Home dir={dir} changeLanguage={changeLanguage} currentLang={currentLang} />}
            />
            <Route
                path="/survey-form-eng"
                element={<SurveyFormEng currentLang={currentLang} />}
            />
            <Route
                path="/survey-form-he"
                element={<SurveyFormHebrew currentLang={currentLang} />}
            />
            <Route
                path="/survey-form-ru"
                element={<SurveyFormRus currentLang={currentLang} />}
            />
            <Route path="*" element={<NotFound404 />} />
        </Routes>
    );
};

export default Main;