                    <FormGroup>
                        <Label>2.3   ( ,   )?</Label>
                        <TextArea
                            name="references"
                            value={formData.references}
                            onChange={handleChange}
                            placeholder=" "
                            required
                        />
                    </FormGroup>
                    <FormGroup>
                        <Label>2.4    ?</Label>
                        <CheckboxGroup>
                            <CheckboxLabel>
                                <input
                                    type="checkbox"
