import React, {useEffect, useState, useRef} from 'react';
import styled from 'styled-components';
import {WHATSAPP} from "./Utils/Constants";
import {useTranslation} from "react-i18next";
import Main from "./Components/Main";

const AppWrapper = styled.div`
    position: relative;
    width: 100vw;
    height: 100%;
    padding: 0;
    margin: 0;
    direction: ${({dir}) => dir};
    display: flex;
    align-items: start;
    justify-content: center;
`;

const WhatsAppImg = styled.img`
    position: fixed;
    ${({dir}) => dir === 'ltr' ? 'right' : 'left'}: 1%;
    bottom: 1%;
    width: 110px;
    height: 110px;
    z-index: 10;
    cursor: pointer;
    opacity: ${(props) => (props.$isScrolling ? "0.4" : "1")};
    transition: opacity 0.3s ease;
`;

function App() {
    const { i18n } = useTranslation();
    const [direction, setDirection] = useState("ltr");
    const [currentLang, setCurrentLang] = useState(i18n.language);
    const [isScrolling, setIsScrolling] = useState(false);
    const scrollTimeoutRef = useRef(null);

    const changeLanguage = (lang) => {
        i18n.changeLanguage(lang);
        setCurrentLang(lang);
        const newDirection = (lang === 'he' || lang === 'iw') ? 'rtl' : 'ltr';
        setDirection(newDirection);
        document.documentElement.dir = newDirection;
    };

    useEffect(() => {
        // Initialize direction based on current language
        const initialDirection = (i18n.language === 'he' || i18n.language === 'iw') ? 'rtl' : 'ltr';
        setDirection(initialDirection);
        document.documentElement.dir = initialDirection;

        const handleScroll = () => {
            setIsScrolling(true);

            if (scrollTimeoutRef.current) {
                clearTimeout(scrollTimeoutRef.current);
            }

            scrollTimeoutRef.current = setTimeout(() => {
                setIsScrolling(false);
            }, 300);
        };

        window.addEventListener("scroll", handleScroll);
        return () => {
            window.removeEventListener("scroll", handleScroll);
            if (scrollTimeoutRef.current) {
                clearTimeout(scrollTimeoutRef.current);
            }
        };
    }, [i18n.language]);

    return (
        <AppWrapper className='row no-gutters g-0' dir={direction}>
            <a
                href="https://wa.me/972547185598"
                target="_blank"
                rel="noopener noreferrer"
                aria-label="Contact via WhatsApp"
            >
                <WhatsAppImg
                    $isScrolling={isScrolling}
                    dir={direction}
                    src={WHATSAPP}
                    alt='WhatsApp contact'
                />
            </a>
            <Main
                dir={direction}
                changeLanguage={changeLanguage}
                currentLang={currentLang}
            />
        </AppWrapper>
    );
}

export default App;